.launcher-container {
  position: absolute;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  background-color: rgba(8, 13, 25, 0.7);
  will-change: transform;
  transition: 0.3s ease transform; }
  .launcher-container.close {
    transform: translateY(280px); }

.launcher-slide {
  display: flex;
  position: relative;
  height: 280px;
  padding: 26px 0 0 25px;
  will-change: transform;
  transition: 0.3s ease transform; }

.launcher-search {
  position: relative;
  width: 69px;
  padding-top: 40px;
  margin-right: 28px; }
  .launcher-search div {
    position: relative;
    border-radius: 50%;
    width: 100%;
    height: 69px;
    background: url("../images/common/ic-search-grey.png") 50% 50% no-repeat;
    background-size: 26px 26px;
    font-size: 0;
    color: rgba(0, 0, 0, 0); }
    .launcher-search div.focus {
      background-color: rgba(255, 255, 255, 0.1);
      background-image: url("../images/common/ic-search-white.png");
      box-shadow: 0 0 0 4px #eee; }

.launcher-profile {
  position: relative;
  width: 182px;
  padding-top: 43px; }
  .launcher-profile > div {
    position: relative;
    padding-top: 73px; }
    .launcher-profile > div::after {
      content: '';
      position: absolute;
      top: 46px;
      left: 2px;
      width: 56px;
      height: 21px;
      margin: 0 auto;
      background-image: url("../images/common/badge-my-kr.png");
      background-size: cover; }
    .launcher-profile > div .ic-profile {
      position: absolute;
      top: 0;
      left: 0;
      width: 61px;
      height: 61px; }
    .launcher-profile > div .launcher-profile-nick {
      font-size: 3.6rem;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis; }
    .launcher-profile > div .launcher-profile-coupon {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #ef3484;
      top: -4px;
      left: 37px;
      height: 29px;
      padding: 0 8px 0 8px;
      border-radius: 14px;
      font-size: 3.2rem; }
    .launcher-profile > div.focus .ic-profile {
      box-shadow: 0 0 0 4px #eee; }

.launcher-indicator-home {
  position: absolute;
  bottom: 21px;
  right: 22px;
  padding-left: 48px;
  font-size: 2.4rem;
  color: #626373; }
  .launcher-indicator-home::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 41px;
    height: 22px;
    background-image: url("../images/common/ic-key-down.png");
    background-size: cover; }

.launcher-contents h2 {
  margin-bottom: 13px;
  font-size: 3.2rem;
  color: #eee; }

.launcher-contents > ul {
  display: flex; }
  .launcher-contents > ul > li {
    position: relative;
    margin-right: 10px; }
    .launcher-contents > ul > li:last-child {
      margin-right: 0; }
    .launcher-contents > ul > li.focus {
      z-index: 5; }

.launcher-contents-watching {
  margin-right: 48px; }

.launcher-contents-recommend ul {
  padding-right: 40px; }
